@use '../base/mixins' as m;
@use '../base/variables' as v;

.dashboard {
    background-color: v.$grisClaro;

    @include m.tablet {
        display: flex;
        height: 100vh;
    }

    .principal {
        flex: 1;
    }

    .contenido {
        padding: 2rem;

        .nombre-pagina {
            font-size: 2.8rem;
            text-align: center;
        }
    }

    .formulario {
        input:not([type="submit"]) {
            background-color: v.$blanco;
        }
        input[type="submit"]{
            @include m.boton(v.$indigo, v.$blanco);
            padding:1rem;
            font-size: 1.4rem;
            font-weight: bold;
            text-transform: uppercase;
            
    }
}

    .cerrar-sesion {
        @include m.boton(darken(v.$indigo, 10%), v.$blanco);
        font-size: 1.2rem;
        padding: 0.5rem;
        border-radius: 0.3rem;

    }
}